<template>
    <div class="shopdetail">
        <header-logo />
        <div class="goback" @click="goback"> <i class="el-icon-arrow-left"></i> Back to Shop</div>
        <div class="product-page ">
            <div class="left">
                <img src="../../../assets/picture/shop03.png" alt="" />
            </div>
            <div class="right">
                <div class="title">Phone</div>
                <div class="price">₱160.00</div>
                <div class="Quantity">Quantity</div>
                <div class="count">
                    <div class="product-quantity-container">
                        <a id="shopreduce" @click="reduceCart" class="change-quantity-icon minus-icon disabled"> - </a>
                        <a id="shopadd"  @click="addCart" class="change-quantity-icon plus-icon"> + </a>
                        <input id="shopnum" v-model="count" class="product-quantity-input ng-pristine ng-untouched ng-valid ng-not-empty">
                    </div>
                </div>
                <div class="AddtoCart">Add to Cart</div>
                <div class="buys">Buy Now</div>
            </div>
        </div>
        <div style="height: 50px"></div>
        <div class="wrapper">
            <Swiper  ref="mySwiper" :options="swiperOptions">
                <swiper-slide class="swiperlist" v-for="index in 10" :key="index">
                    <router-link to="/shop/detail">
                        <img src="../../../assets/picture/shopimg.png" alt="">
                    </router-link>
                </swiper-slide>

                <div class="swiper-button-prev" slot="button-prev" @click="prev"></div>
                <div class="swiper-button-next" slot="button-next" @click="next"></div>
            </Swiper>
        </div>
        <div style="height: 20px"></div>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
import HeaderLogo from '../Layout/HeaderLogo'
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/swiper-bundle.css";

export default {
    components: {
        Swiper,
        SwiperSlide,
        HeaderLogo
    },
    computed: {
        swiper() {
            return this.$refs.mySwiper.$swiper
        }
    },
    data() {
        return {
            count: 1,
            swiperOptions: {
                loop: true,
                navigation: {
                    nextEl: ".swiper-button-next",
                    prevEl: ".swiper-button-prev",
                }
            }
        }
    },
    beforeCreate() {
        localStorage.setItem("activepage", "shop");
    },
    methods: {
        ...mapMutations(['saveCartList']),
        goback(){
            this.$router.go(-1);
        },
        prev(){
            this.swiper.slidePrev();
        },
        next(){
            this.swiper.slideNext();
        },
        reduceCart(){
            if(this.count>1){
                this.count -= 1;
            }
        },
        addCart(){
            this.count += 1;
        }
    },
};
</script>

<style lang="less" scoped>

.shopdetail{
    width: 80%;
    margin: 0 auto;
}
.goback{
    margin: 20px 0;
    color: rgb(47, 47, 48);
    font-size: 16px;
    letter-spacing: 0.4;
}
.product-page {
    .right{
        margin-top: 25px;
        .title{
            color: rgb(124, 73, 56);
            font-size: 30px;
        }
        .price{
            color: rgb(95, 95, 95);
            font-size: 24px;
            margin-top: 10px;
        }
        .Quantity{
            color: rgb(78, 128, 241);
            font-size: 14px;
            margin: 20px 0;
        }
        // count
        .product-quantity-container {
            position: relative;
            width: 64px;
            height: 28px;
        }
        .change-quantity-icon.minus-icon {
            position: absolute;
            left: 5px;
            top: 5px;
        }
        .change-quantity-icon.plus-icon {
            position: absolute;
            right: 5px;
            top: 5px;
        }
        .change-quantity-icon {
            font-style: normal;
            font-weight: 400;
            font-variant: normal;
            text-transform: none;
            line-height: 1;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            font-family: glyphicons;
            font-size: 16px;
        }
        .product-quantity-input {
            border: 1px solid #2F2F30;
            color: #2F2F30;
            background-color: #FFFFFF;
            padding-left: 0;
            padding-right: 0;
            text-align: center;
            vertical-align: middle;
            font-size: 12px;
            width: 64px;
            height: 28px;
        }
        .AddtoCart{
            font: normal normal normal 17px/1.4em avenir-lt-w01_35-light1475496,sans-serif;
            color: #FFFFFF;
            background-color: rgb(29, 145, 168);
            padding: 10px 16px;
            text-align: center;
            margin-top: 20px;
            width: 100%;
        }
        .buys{
            font: normal normal normal 17px/1.4em avenir-lt-w01_35-light1475496,sans-serif;
            color: #FFFFFF;
            background-color: #D95932;
            padding: 10px 16px;
            text-align: center;
            margin-top: 20px;
            width: 100%;
        }
    }
}
.wrapper{
    img{
        width: 100%;
        height: 100%;
    }
}
</style>